@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-medium.woff2") format("woff2"), url("../fonts/montserrat-medium.woff") format("woff");
  font-weight: 500;
  font-style: normal; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-semibold.woff2") format("woff2"), url("../fonts/montserrat-semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal; }

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  font-style: normal;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  font-family: "Montserrat", "Arial", sans-serif; }

.page-header {
  position: relative;
  z-index: 10;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
  max-width: 100%;
  padding-top: 35px;
  padding-bottom: 35px;
  padding-left: 103px;
  padding-right: 76px; }

@media screen and (max-width: 1023px) {
  .page-header {
    padding-top: 30px;
    padding-bottom: 30px;
    padding-left: 45px;
    padding-right: 45px; } }

@media screen and (max-width: 767px) {
  .page-header {
    flex-direction: column;
    padding-top: 20px;
    padding-left: 15px;
    padding-right: 15px; } }

@media screen and (max-width: 1023px) {
  .page-header__logo-image {
    width: 38px;
    height: 20px; } }

@media screen and (max-width: 767px) {
  .page-header__logo-image {
    position: relative;
    width: 42px;
    height: 22px;
    margin-bottom: 29px;
    z-index: 20; } }

.main-nav {
  display: flex; }

.main-nav__toggle {
  display: none; }

@media screen and (max-width: 767px) {
  .main-nav__wrapper {
    min-height: 299px;
    background-color: #E5E5E5; }
  .main-nav__toggle {
    position: absolute;
    top: 9.5px;
    right: 7px;
    z-index: 12;
    display: block;
    width: 40px;
    height: 40px;
    background-color: transparent;
    cursor: pointer;
    border: none; }
  .main-nav--closed .main-nav__toggle::before {
    content: "";
    position: absolute;
    top: 13px;
    right: 8px;
    width: 24px;
    height: 2px;
    background-color: #ffffff;
    box-shadow: 0 6px 0 0 #ffffff, 0 12px 0 0 #ffffff; }
  .main-nav--closed .main-nav__toggle:hover::before {
    background-color: rgba(255, 255, 255, 0.8);
    box-shadow: 0 6px 0 0 rgba(255, 255, 255, 0.8), 0 12px 0 0 rgba(255, 255, 255, 0.8); }
  .main-nav--closed .main-nav__toggle:active::before {
    background-color: rgba(255, 255, 255, 0.5);
    box-shadow: 0 6px 0 0 rgba(255, 255, 255, 0.5), 0 12px 0 0 rgba(255, 255, 255, 0.5); }
  .main-nav--opened .main-nav__toggle {
    top: 9.5px;
    right: 7px; }
    .main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
      content: "";
      position: absolute;
      top: 19px;
      right: 10px;
      width: 20px;
      height: 2px;
      background-color: black; }
    .main-nav--opened .main-nav__toggle::before {
      transform: rotate(45deg);
      box-shadow: none; }
    .main-nav--opened .main-nav__toggle::after {
      transform: rotate(-45deg); }
    .main-nav--opened .main-nav__toggle:hover::before, .main-nav--opened .main-nav__toggle:hover::after {
      opacity: 0.8; }
    .main-nav--opened .main-nav__toggle:active::before, .main-nav--opened .main-nav__toggle:active::after {
      opacity: 0.5; }
  .main-nav--closed .site-list__item {
    display: none; }
  .main-nav--opened .main-nav__wrapper {
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    padding-top: 73px;
    padding-left: 14px;
    z-index: 10; }
  .main-nav--nojs .main-nav__toggle {
    display: none; }
  .main-nav--nojs .main-nav__wrapper {
    position: static;
    min-height: 0; }
  .main-nav--nojs .site-list__item {
    display: block; } }

.site-list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none; }

@media screen and (max-width: 767px) {
  .site-list {
    flex-direction: column; } }

.site-list a {
  text-decoration: none; }

.site-list__item {
  padding-top: 8px;
  padding-bottom: 8px; }

@media screen and (max-width: 1023px) {
  .site-list__item {
    padding-top: 0px;
    padding-bottom: 0px; } }

.site-list__link {
  margin-right: 27px;
  margin-left: 27px;
  font-weight: 500;
  color: #F9FBFD;
  line-height: 20px;
  transition: .3s linear; }

.site-list__link:hover {
  opacity: 0.8; }

.site-list__link:active {
  opacity: 0.5; }

@media screen and (max-width: 1023px) {
  .site-list__link {
    margin-right: 15px;
    margin-left: 15px;
    font-size: 14px;
    line-height: 17px; } }

@media screen and (max-width: 767px) {
  .site-list__link {
    display: flex;
    margin: 0;
    padding: 0;
    margin-top: 10.2px;
    margin-bottom: 10.2px;
    font-size: 18px;
    line-height: 22px;
    color: #011C40; } }

.page-main {
  min-height: 1000px; }

.about-us img {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: auto;
  min-height: 650px; }

@media screen and (max-width: 1023px) {
  .about-us img {
    min-height: 638px; } }

@media screen and (max-width: 767px) {
  .about-us img {
    min-height: 500px; } }

.page-footer {
  display: flex;
  background-color: #011C40; }

.nav__list {
  display: flex;
  padding: 0;
  margin: 0; }

.social__list {
  display: flex;
  padding: 0;
  margin: 0; }
